
/**
 *  –®–∞–±–ª–æ–Ω –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ
 *  @param {Object} user - —Å–æ—Ç—Ä—É–¥–Ω–∏–∫
 *  @param {number} [user.idUser] - id
 *  @param {string} [user.fio] - –§–ò–û
 *  @param {string} [user.division] - –¥–∏–≤–∏–∑–∏–æ–Ω
 *  @param {string} [user.department] - –æ—Ç–¥–µ–ª
 *  @param {string} [user.work] - –Ω–∞–∑–≤–∞–Ω–∏—è –¥–æ–ª–∂–Ω–æ—Å—Ç–∏
 *  @param {string} [user.status] - –Ω–∞–∑–≤–∞–Ω–∏—è –¥–æ–ª–∂–Ω–æ—Å—Ç–∏
 *  @param {Object} options - –Ω–∞—Å—Ç—Ä–æ–π–∫–∏
 *  @param {number} [options.build] - –≤ –∫–∞–∫–æ–π –∫–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏–∏ —Å–æ–±–∏—Ä–∞—Ç—å —Å–æ—Ç—Ä—É–¥–Ω–∏–∫–∞ 1-–∫–∞—Å—Ç–æ–º–Ω—ã–π, 2-–∏–∑ –ë–•, 0-–∏–∑ –ë–• –±–µ–∑ Hse
 *  @return {string}
 * */
export function userInfoTemplate({idUser,fio,division,department,work,status},
                                 {build = 0} = {}
) {

  /**
   * –ö–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
   * @return {string}
   */
  const renderUserConfig = () => {

    // –∫–∞—Å—Ç–æ–º–Ω—ã–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å
    if(build === 1){
      return  `
        <div class="col-4 g-justify-items-left js-matrix-work-hse" title="${work || '–Ω–µ –∑–∞–ø–æ–ª–Ω–µ–Ω–æ'}">
          <span class="result__clip">
            ${work || '–Ω–µ –∑–∞–ø–æ–ª–Ω–µ–Ω–æ'}
          </span>
        </div>
      `
    }
    // —Å—É—â–µ—Å—Ç–≤—É—é—â–∏–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –∏–∑ BX
    else if (build === 2) {
      return  `
        <div class="col-4 g-justify-items-left js-matrix-work-hse" title="${work || '–Ω–µ –∑–∞–ø–æ–ª–Ω–µ–Ω–æ'}">
          <span class="result__clip">
            ${work || '–Ω–µ –∑–∞–ø–æ–ª–Ω–µ–Ω–æ'}
          </span>
        </div>
      `
    }
    // —Å—É—â–µ—Å—Ç–≤—É—é—â–∏–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –∏–∑ BX –±–µ–∑ HSE
    else {
      return `
        <div class="col-4 js-matrix-work-hse">
          <span class="p-relative d-inline-block">
            <button class="button button--icon js-add-hse-modal" type="button" data-sumbiot-target="#add-hse-modal" data-id="${idUser}" title="–î–æ–±–∞–≤–∏—Ç—å –¥–æ–ª–∂–Ω–æ—Å—Ç—å HSE">
              <img class="result__img" src="${BX.message('TemplateFolder')}/assets/img/add-document-icon.svg" width="18" height="18" alt="">
            </button>
          </span>
        </div>
      `
    }
  }

  return `
    <div class="col-1" title="ID: ${idUser}"></div>
    <div class="col-2" title="–û—Ç–¥–µ–ª: ${department || '–Ω–µ –∑–∞–ø–æ–ª–Ω–µ–Ω–æ'}">
      <span class="result__clip text-align-center">
        ${division || '–Ω–µ –∑–∞–ø–æ–ª–Ω–µ–Ω–æ'}
      </span>
    </div>
    <div class="col-3 g-justify-items-left" title="${fio || '–Ω–µ –∑–∞–ø–æ–ª–Ω–µ–Ω–æ'}">
      <span class="result__clip">
        ${fio || '–Ω–µ –∑–∞–ø–æ–ª–Ω–µ–Ω–æ'}
      </span>
    </div>
    ${renderUserConfig()}
    <div class="col-1">${status || '–Ω–µ –∑–∞–ø–æ–ª–Ω–µ–Ω–æ'}</div>
    <div class="col-1">
      <button class="result__options-arrow button button--arrow" type="button" title="–ü–æ–¥—Ä–æ–±–Ω–æ"></button>
    </div>
  `
}
